body {
    color: #333333;
    background-color: white;
    margin: 24px;
    font-family: sans-serif;
}

.panel {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    position: fixed;
    left: 0;
    top: 0;
    width: 300px;
    height: 100%;
    background-color: #f4f4f4;
    padding: 20px;
}

.roundCounter.hidden {
    display: none;
}

.game-content {
    margin-left: 260px;
    padding: 20px;
}

.game-area {
    justify-content: center;
    display: flex;
}

.imageSelect {
    display: grid;
    grid-template-columns: repeat(2, 128px);
    grid-gap: 10px;
    justify-content: center;
    padding: 50px;
}

.board {
    display: grid;
    grid-template-columns: repeat(4, 128px);
    grid-gap: 10px;
    justify-content: center;
}

.board.width4 {
    grid-template-columns: repeat(4, 128px);
}

.board.width6 {
    grid-template-columns: repeat(6, 128px);
}

.dialog {
    top: 34%;
    position: absolute;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    width: 350px;
    padding: 20px;
    margin: 0 auto;
    background-color: #f4f4f4;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.5);
    z-index: 1000;
}

.dialog.hidden {
    display: none;
}

.card {
    width: 128px;
    height: 196px;
    background-color: #666666;
    margin: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    transition: background-color 0.3s;
    transition: border 0.3s;
    border: 4px solid transparent;
    box-sizing: border-box;
    margin: 0px
}

.card:hover {
    background-color: #333333;
}

.card.revealed,
.card.chosen {
    background-color: #eeeeee;
    /* Background for revealed and chosen cards */
    color: black;
    /* Make the text visible */
    border: 4px solid #666666;
    /* Dark border for chosen cards */
}

.card.revealed:hover {
    background-color: #eeeeee;
    /* Revealed cards do not change on hover */
    cursor: default;
    /* Cursor should not indicate 'clickable' for revealed cards */
}

.card.hidden {
    color: transparent;
    cursor: pointer;
}

.card.hidden:hover {
    background-color: #333333;
    border: 4px solid #333333;
    /* Only hidden cards should highlight on hover */
}

.card.hidden.space {
    background: url(img/space.jpg);
}

.card.hidden.flowers {
    background: url(img/flowers.jpg);
}

.card.hidden svg {
    visibility: hidden;
    /* Hide SVG for hidden cards */
}

.card.revealed svg,
.card.chosen svg {
    visibility: visible;
    /* Only show SVG for revealed or chosen cards */
}